import React from 'react';

export default ({placeholder, id}) => {
  return <input className="form__input" type="text" id={id} name={id} placeholder={placeholder} pattern={'[0-9]{1,3}|1000'} required={true} onInput={(evt)=> {

    evt.target.setCustomValidity('');
    if (!evt.target.checkValidity()) {
      evt.target.setCustomValidity('   1  1000 ');
      evt.target.reportValidity();
    }
  }}/>
}
